import { useEffect, useRef } from "react";
// import Auth from "../../Auth";
import DataTable from "../../Components/DataTable";
import fetch from "../../Services/fetch";

const $ =  window.$;

const CheckBox = (userId, pageId, isChecked = false, defaultVal={}) => {
  return `<input class="pageCheckBox" type="checkbox" 
  name="${userId}"
  data-id='${pageId}'
  data-obj='${defaultVal}'
  ${isChecked? 'checked': ''}
  />`;
}

const PageAuthorization = () => {

  const tblRef = useRef();
  
  const header = [
    {title: 'First Name', data: 'firstName'},
    // {title: 'Middle Name', data: 'middleName'},
    {title: 'Last Name', data: 'lastName'},
    {title: 'Email', data: 'email'},
    {title: 'SAQ Sites', orderable: false, data: 'pages.1'},
    {title: 'SAQ Files', orderable: false, data: 'pages.2'},
    {title: 'Site Evaluation Summary', orderable: false, data: 'pages.3'},
    {title: 'Partner Evaluation Summary', orderable: false, data: 'pages.4'},
    {title: 'Site Technical Evaluation', orderable: false, data: 'pages.5'},
    {title: 'Site Technical Summary', orderable: false, data: 'pages.6'},
    {title: 'Site Construction', orderable: false, data: 'pages.7'},
    {title: 'Site legal asssestment', orderable: false, data: 'pages.8'},
    {title: 'Site legal Summary', orderable: false, data: 'pages.9'},
    {title: 'Approve Sites', orderable: false, data: 'pages.10'},
    
    
  ];
  
  useEffect(()=>{
    // get user data
    fetch.get(`${process.env.REACT_APP_HOST}/api/page/users`)
    .then(async res=>{
      const result = [];
      for (const iterator of res.data) {
        const pages = JSON.stringify(iterator.pages);
        console.log(pages);
        result.push({
          ...iterator, 
          'pages': {
              // userId, pageId, isChecked = false, defaultVal={}
               1: CheckBox(
                 iterator.userId,
                 1, 
                 iterator.pages['1'],
                 pages
              ),
              2: CheckBox(
                iterator.userId,
                2, 
                iterator.pages['2'],
                pages
              ),
              3: CheckBox(
                iterator.userId,
                3,
                iterator.pages['3'],
                pages 
              ),
              4: CheckBox(
                iterator.userId,
                4,
                iterator.pages['4'],
                pages 
              ),
              5: CheckBox(
                iterator.userId,
                5,
                iterator.pages['5'],
                pages 
              ),
              6: CheckBox(
                iterator.userId,
                6,
                iterator.pages['6'],
                pages 
              ),
              7: CheckBox(
                iterator.userId,
                7,
                iterator.pages['7'],
                pages 
              ),
              8: CheckBox(
                iterator.userId,
                8,
                iterator.pages['8'],
                pages 
              ),
              9: CheckBox(
                iterator.userId,
                9,
                iterator.pages['9'],
                pages 
              ),
              10: CheckBox(
                iterator.userId,
                10,
                iterator.pages['10'],
                pages 
              ),
          },
        });
        
      }
      await tblRef.current.init(result);

      // add event listener
      $(".pageCheckBox").on('change', function(){
        console.log($(this).data('id'),'pageCheckBox', $(this).attr("name"));
        const userId = $(this).attr("name");
        const pageId = $(this).data('id');
        const isChecked = $(this).prop('checked');
        
        const defaultVal = $(this).closest('tr').data('obj') || $(this).data('obj');
        defaultVal[pageId] = isChecked;
        
        // console.log(defaultVal, pageId);
        fetch.post(`${process.env.REACT_APP_HOST}/api/page/${userId}/update`, defaultVal)
        .then(()=>{
          $(this).closest('tr').attr('data-obj', JSON.stringify(defaultVal));
        })
        .catch(er=>console.log(er));
      });
    })
    .catch(er=>{
      console.log(er)
    })

    // modal
    $('#staticBackdrop').on('hidden.bs.modal',(e) => {
      // setIsUpdate(false); 
    });
    
  },[]);
  
  return (
    <>
        {/* data table */}
        <div className="card mt-3 card-outline card-primary">
            <div className="card-header">
            <h3 className="card-title">Employee Lists</h3>
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body">
                <DataTable 
                  // getSelected={getSelected}
                  header={header}
                  ref={tblRef}
                />
            </div>
            {/* <!-- /.card-body --> */}
        </div>
    </>
   );
}
 
export default PageAuthorization;