import { useEffect } from "react";
import { NavLink } from "react-router-dom";
const $ = window.$;
const OperationNav = ({props}) => {
  useEffect(()=>{
    // control the arrow symbol for collapse
    $(".collapse").on('show.bs.collapse', function(){
        $(this).parent().children(':first-child').addClass('collapse-open');
    });
    $(".collapse").on('hide.bs.collapse', function(e){
        $(e.target).parent().children(':first-child').removeClass('collapse-open');
    });
  },[props]);

  if(!props.hasOwnProperty('operation'))
  return <></>;
  return props.hasOwnProperty('operation') && ( 
  <li className="nav-item">
    <a href="/#" className="nav-link" data-toggle="collapse" data-target="#sidenav-collapse-operation-0">
    <i className="nav-icon fas fa-hands-helping"></i>
    <p>
        Operation
        <i className="fas fa-angle-left right"></i>
    </p>
    </a>
    <ul className="nav collapse" id="sidenav-collapse-operation-0">
      {
            props.operation.OPERATION.map(saq =>(
                <li className="nav-item" key={saq.navlink}>
                    <NavLink exact to={saq.navlink} className="nav-link">
                    <i className="far fa-dot-circle nav-icon"></i>
                    <p>{saq.title || ''}</p>
                    </NavLink>
                </li>
            ))
        }
    </ul>
  </li>
 );
}
 
export default OperationNav;