/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import Auth from "../../Auth";
import Modal from "../../Components/Modal";
import UploadSites from "../../Components/UploadSites";
import fetch from '../../Services/fetch';
import ExportXls from "../../Components/ExportXls";
import { CONSTANT } from "../../Services/String";
import SiteCount from "../../Components/SiteCount";
const moment = window.moment;
const google = window.google;
const $ =  window.$;
const toastr = window.toastr;
const markerContainer = {};
const infowindowContainer = []; 
const SAQSites = () => {

  const[siteCount, setSiteCount] = useState({
    TotalApproved: 1,
    TotalPending: 0,
    TotalRejected: 0,
    TotalSites: 0,
  })
  const [showSearch, setShowSearch] = useState(false);
  const [search, setSearch] = useState('');
  const [searchList, setSearchList] = useState([]);
  

  const [map, setMap] = useState('');
  const[locations, setLocations] = useState([]);
  const[site, setSite] = useState({
    location: '',
    address: '',
    _saqfileurl: '',
    siteContactPerson: '',
    siteContactNum: '',
    _idcontractor: '',
    contractor: '',
    taxDec: false,
    taxClearance: false,
    sketchPlan: false,
    contactPerID: false,
    coordPhoto: false,
    photoLandOwner: false,
    visinityPlan: false,
    LIS: false,
  });

  // udpate search list on changes of locations and search keywords
  useEffect(()=>{
    let count = 0;
    const searchResult = locations.filter(map => {
      return (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
        && count++ < 10;
      // return (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
      // if(map.address && map.siteContactPerson){
      //   return (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
      //   && count++ < 10;
      // }
      // if(map.address){
      //   return (map.address.toLowerCase().includes(search))
      //   && count++ < 10;
      // }
      
      // return (map.siteContactPerson.toLowerCase().includes(search))
      // && count++ < 10;
    }
      // (map.address.toLowerCase().includes(search) || map.siteContactPerson.toLowerCase().includes(search))
      // && count++ < 10
    );
    setSearchList(searchResult)
  }, [locations, search])
  
  // pull map records
  useEffect(()=>{
    fetch.get(`${process.env.REACT_APP_HOST}/api/site/1/stat`)
    .then(res => {
      setLocations(res.data);
    })
    .catch(er=>{
      console.log(er);
      toastr.error('An error encountered, please try again')
    })

    // bug fixed show modal while infowindow is open
    $(".modal").on('show.bs.modal', function(){
      for (const iterator of infowindowContainer) {
        iterator.close();
      }
    });
    

    // get site count
    fetch.get(`${process.env.REACT_APP_HOST}/api/site/count`)
    .then(res=>{
      setSiteCount(res.data);
    })
    .catch(e=>console.log(e))
  },[]);

  // init map
  useEffect(() => {
    const map = new google.maps.Map(document.getElementById("map"), {
      center: { lat: 10.714222484489797, lng: 123.92758604369578 },
      zoom: 6,
    });
    setMap(map);
    
    // let marker;
    
    for (let i = 0; i < locations.length; i++) {  
      addMarker(map, locations[i], infowindowContainer, markerContainer);
    }
  }, [locations]);

  const addMarker = (map, location, infowindowContainer, markerContainer) => {
    
    // read only button
    const readOnly = Auth.role !== 'standard';

    // info window
    //  let infowindow = new google.maps.InfoWindow(); 
     
      
      let coor = location.location.split(',');
      //  console.log(coor);
       let marker = new google.maps.Marker({
         position: new google.maps.LatLng(Number(coor[0]), Number(coor[1])),
         map: map
       });
       markerContainer[location.location] = marker;
      // const icon = {

      //   path: "M 150.94 192 h 33.73 c 11.01 0 18.61 -10.83 14.86 -21.18 c -4.93 -13.58 -7.55 -27.98 -7.55 -42.82 s 2.62 -29.24 7.55 -42.82 C 203.29 74.83 195.68 64 184.67 64 h -33.73 c -7.01 0 -13.46 4.49 -15.41 11.23 C 130.64 92.21 128 109.88 128 128 c 0 18.12 2.64 35.79 7.54 52.76 c 1.94 6.74 8.39 11.24 15.4 11.24 Z M 89.92 23.34 C 95.56 12.72 87.97 0 75.96 0 H 40.63 c -6.27 0 -12.14 3.59 -14.74 9.31 C 9.4 45.54 0 85.65 0 128 c 0 24.75 3.12 68.33 26.69 118.86 c 2.62 5.63 8.42 9.14 14.61 9.14 h 34.84 c 12.02 0 19.61 -12.74 13.95 -23.37 c -49.78 -93.32 -16.71 -178.15 -0.17 -209.29 Z M 614.06 9.29 C 611.46 3.58 605.6 0 599.33 0 h -35.42 c -11.98 0 -19.66 12.66 -14.02 23.25 c 18.27 34.29 48.42 119.42 0.28 209.23 c -5.72 10.68 1.8 23.52 13.91 23.52 h 35.23 c 6.27 0 12.13 -3.58 14.73 -9.29 C 630.57 210.48 640 170.36 640 128 s -9.42 -82.48 -25.94 -118.71 Z M 489.06 64 h -33.73 c -11.01 0 -18.61 10.83 -14.86 21.18 c 4.93 13.58 7.55 27.98 7.55 42.82 s -2.62 29.24 -7.55 42.82 c -3.76 10.35 3.85 21.18 14.86 21.18 h 33.73 c 7.02 0 13.46 -4.49 15.41 -11.24 c 4.9 -16.97 7.53 -34.64 7.53 -52.76 c 0 -18.12 -2.64 -35.79 -7.54 -52.76 c -1.94 -6.75 -8.39 -11.24 -15.4 -11.24 Z m -116.3 100.12 c 7.05 -10.29 11.2 -22.71 11.2 -36.12 c 0 -35.35 -28.63 -64 -63.96 -64 c -35.32 0 -63.96 28.65 -63.96 64 c 0 13.41 4.15 25.83 11.2 36.12 l -130.5 313.41 c -3.4 8.15 0.46 17.52 8.61 20.92 l 29.51 12.31 c 8.15 3.4 17.52 -0.46 20.91 -8.61 L 244.96 384 h 150.07 l 49.2 118.15 c 3.4 8.16 12.76 12.01 20.91 8.61 l 29.51 -12.31 c 8.15 -3.4 12 -12.77 8.61 -20.92 l -130.5 -313.41 Z M 271.62 320 L 320 203.81 L 368.38 320 h -96.76 Z",
      //   // fillColor: '#FF0000',
      //   fillOpacity: .6,
      //   anchor: new google.maps.Point(0,0),
      //   strokeWeight: 0,
      //   scale: 1,
      //   // width: 4,
      //   // height: 4,
      // }

      // marker = new google.maps.Marker({
      //   position: new google.maps.LatLng(Number(coor[0]), Number(coor[1])),
      //   icon: icon,
      //   map: map,
        // label: {
        //   fontFamily: "'Font Awesome 5 Free'",
        //   text: '\uf0f9', //icon code
        //   fontWeight: '900', //careful! some icons in FA5 only exist for specific font weights
        //   // color: '#FFFFFF', //color of the text inside marker
        // },
        // labelContent: '<i class="fa fa-send fa-3x" style="color:rgba(153,102,102,0.8);"></i>',
        // labelAnchor: new google.maps.Point(22, 50),
        // labelClass: "labels" // the CSS class for the label
      // });

       const contentString = `
            <form id="infowindowMap">
             <div class="container">
               <div class="row mt-3">
                  <div class="form-group col-12">
                     <label htmlFor="exampleInputEmail1">
                       Site Docs/<a href=${location._saqfileurl ? location._saqfileurl : process.env.REACT_APP_CDRIVE} target="_new">Check Docs</a>
                     </label>
                     <span style="float:right">${location.auditPercent}% completed</span>
                     <input 
                     value="${location._saqfileurl}"
                     ${readOnly && 'readOnly'}
                     name="_saqfileurl"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Doc url link" />
                 </div>
                  <div class="form-group col-12">
                     <label htmlFor="exampleInputEmail1">Site Address</label>
                     <input 
                     value="${location.address}"
                     ${readOnly && 'readOnly'}
                     name="address"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Contact Person</label>
                     <input 
                     value="${location.siteContactPerson}"
                     ${readOnly && 'readOnly'}
                     name="siteContactPerson"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Contact Number</label>
                     <input 
                     value="${location.siteContactNum}"
                     ${readOnly && 'readOnly'}
                     name="siteContactNum"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Coordinates</label>
                     <input 
                     value="${coor.join(', ')}"
                     readOnly
                     name="location"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 
                 <div class="form-group col-6">
                     <label htmlFor="exampleInputEmail1">Coordinator</label>
                     <input 
                     value="${location._idcontractor}"
                     ${readOnly && 'readOnly'}
                     name="_idcontractor"
                     type="text" class="form-control" id="exampleInputEmail1" placeholder="Name" />
                 </div>
                 <div class="${ location.status !== 0 ? "d-none": "form-group col-12"} style="width: 100%">
                    <label htmlFor="exampleInputEmail1">Remarks</label>
                    <textarea class="form-control" rows="3" placeholder="Type here ..." 
                    readOnly
                    name="actionRemarks">${location.actionRemarks}</textarea>
                </div>
                <!---checklist --->
                <div class="col-12 pb-1 mb-4 mt-2 font-weight-bold legend-group text-secondary">
                  <span data-toggle="collapse" data-target="#updatesitechecklist" class="cursor-pointer">
                    <i class="fas fa-tasks"></i> &nbsp;Checklist
                  </span>
                </div>
                <div class="col-12">
                    <div id="updatesitechecklist" class="collapse row">
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="taxDec">Tax Dec/Land Title
                              <input id="taxDec" name="taxDec" type="checkbox" value="1" ${location.taxDec && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="taxClearance">Tax Clearance
                              <input id="taxClearance" name="taxClearance" type="checkbox" value="1" ${location.taxClearance && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="sketchPlan">Sketch Plan
                              <input id="sketchPlan" name="sketchPlan" type="checkbox" value="1" ${location.sketchPlan && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="contactPerID">Contact person ID with 3 signature
                              <input id="contactPerID" name="contactPerID" type="checkbox" value="1" ${location.contactPerID && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="coordPhoto">Coordinates Photo
                              <input id="coordPhoto" name="coordPhoto" type="checkbox" value="1" ${location.coordPhoto && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="photoLandOwner">Photo with land owner
                              <input id="photoLandOwner" name="photoLandOwner" type="checkbox" value="1" ${location.photoLandOwner && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="visinityPlan">Visinity Plan
                              <input id="visinityPlan" name="visinityPlan" type="checkbox" value="1" ${location.visinityPlan && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                      <div class="col-6 form-group">
                          <label class="checkboxcontainer" for="LIS">Lease Information Sheet (LIS)
                              <input id="LIS" name="LIS" type="checkbox" value="1" ${location.LIS && 'checked'} >
                              <span class="checkmark"></span>
                          </label>
                      </div>
                    </div>
                </div>

                <!---end checklist --->
               </div>
               <div class="form-group col-12 text-right">
                  <div class="${ location.status !== 1 || location.docstat === 0 ? "d-none": ""}">
                    <button class="btn btn-danger" id="btn0">Delete</button>   
                    <button class="btn btn-primary" id="btn1">Update</button>
                  </div>
                  <button class="${ location.docstat !== 0 ? "d-none": "btn btn-primary"}" id="btn-restore">Restore</button>
               </div>
             </div>
           </form>`;

      //  const self = this;
      
       marker.addListener('click', function() {
          // if (typeof infowindow != 'undefined') infowindow.close();
          let infowindow = new google.maps.InfoWindow({
            content: contentString,
          })
          infowindowContainer.push(infowindow);
          for (const iterator of infowindowContainer) {
              iterator.close();
          }
          infowindow.open(map, marker);

          google.maps.event.addListener(infowindow, 'domready', function() {
              //  update
              document.getElementById("btn1").addEventListener("click", async (e)=>{
                e.preventDefault();
                
                const form = document.querySelector('#infowindowMap')
                const entries = Object.fromEntries(new FormData(form).entries());
                entries['_updatedby'] = Auth._id;
                entries['updated_at'] = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
                entries['status'] = 1;
                delete entries.contractorName;

                // calculate percentage
                let auditPercent = (entries.location? 1 : 0) +
                                    (entries.address? 1 : 0) + 
                                    (entries.siteContactPerson? 1 : 0) +
                                    (entries.siteContactNum? 1 : 0) +
                                    (entries._idcontractor? 1 : 0) +
                                    (entries._saqfileurl? 1 : 0) +
                                    (entries.taxDec? 1 : 0) +
                                    (entries.taxClearance? 1 : 0) +
                                    (entries.sketchPlan? 1 : 0) +
                                    (entries.contactPerID? 1 : 0) +
                                    (entries.coordPhoto? 1 : 0) +
                                    (entries.photoLandOwner? 1 : 0) +
                                    (entries.visinityPlan? 1 : 0) +
                                    (entries.LIS? 1 : 0);
                auditPercent = ((auditPercent / 14 ) * 100 ).toFixed(0);
                entries['auditPercent'] = auditPercent;
                
                // update
                const res = await fetch.post(`${process.env.REACT_APP_HOST}/api/site/${location.location}/update`, entries);
                if(res.status === 200){
                  toastr.success(CONSTANT.SUCCESS);
                  const locIdex  = locations.findIndex(loc=>loc.location === location.location);
                  locations[locIdex] = {...locations[locIdex], ...entries};
                  setLocations([]);
                  setLocations(locations);
                  infowindow.close();
                }else{
                  toastr.error(CONSTANT.ERRROR);
                }
              });

              //  delete
              document.getElementById("btn0").addEventListener("click", async (e)=>{
                e.preventDefault();
                if(!window.confirm("Are you sure you want to delete this location?"))
                return;

                const res = await fetch.post(`${process.env.REACT_APP_HOST}/api/site/${location.location}/update`, {
                  docstat: 0
                });
                if(res.status === 200){
                  const tempLocation = locations.filter(loc => loc.location !== location.location);
                  setLocations(tempLocation);
                  toastr.success(CONSTANT.SUCCESS); 
                } else{
                  toastr.error(CONSTANT.ERRROR); 
                }
              });

              //  restore
              document.getElementById("btn-restore").addEventListener("click", async (e)=>{
                e.preventDefault();
                const res = await fetch.post(`${process.env.REACT_APP_HOST}/api/site/${location.location}/update`, {
                  docstat: 1
                });
                if(res.status === 200){
                  const tempLocation = locations.filter(loc => loc.location !== location.location);
                  setLocations(tempLocation);
                  toastr.success(CONSTANT.SUCCESS); 
                } else{
                  toastr.error(CONSTANT.ERRROR); 
                }
              });
          });

          // change zoom settings
          // map.setCenter(marker.getPosition());
          // console.log(marker.getPosition());
          // map.setZoom(10);
       });
  }

  const newMarker = async () =>{
    let auditPercent = (site.location? 1 : 0) +
                       (site.address? 1 : 0) + 
                       (site.siteContactPerson? 1 : 0) +
                       (site.siteContactNum? 1 : 0) +
                       (site._idcontractor? 1 : 0) +
                       (site._saqfileurl? 1 : 0) +
                       (site.taxDec || 0) +
                       (site.taxClearance || 0) +
                       (site.sketchPlan || 0) +
                       (site.contactPerID || 0) +
                       (site.coordPhoto || 0) +
                       (site.photoLandOwner || 0) +
                       (site.visinityPlan || 0) +
                       (site.LIS || 0);
    auditPercent = ((auditPercent / 14 ) * 100 ).toFixed(0);
    const marker = await fetch.post(`${process.env.REACT_APP_HOST}/api/site/create`,
      {
        location: (site.location).replace(/ /g, "").trim(),
        address: site.address,
        siteContactPerson: site.siteContactPerson,
        siteContactNum: site.siteContactNum,
        _idcontractor: site._idcontractor,
        _saqfileurl: site._saqfileurl,
        _createdby: Auth._id,
        taxDec: site.taxDec,
        taxClearance: site.taxClearance,
        sketchPlan: site.sketchPlan,
        contactPerID: site.contactPerID,
        coordPhoto: site.coordPhoto,
        photoLandOwner: site.photoLandOwner,
        visinityPlan: site.visinityPlan,
        LIS: site.LIS,
        auditPercent: auditPercent,
        status: 1,
      });
    
      if(marker.status === 400)
      return toastr.error('Location already exist');

      setLocations([...locations, marker.data]);
      $('#staticBackdrop').modal('hide');
  }

  const handleChange = (e) => {
    const target =  e.target;
    
    if(target.name === 'location')
    target.value = (target.value).replace(/ /g, "");
   
    if(target.type === 'checkbox'){
      setSite({...site, [target.name] : target.checked });
    }else{
      setSite({...site, [target.name] : target.value });
    }
  }

  // set focus show search sidebar and focus on clicked search result
  const setFocus = async (location = null) => {
    setShowSearch(true);
    if(location){
      location = await location.split(",")
      
      // animate
      markerContainer[location].setAnimation(google.maps.Animation.BOUNCE);
      setTimeout(function() {
        markerContainer[location].setAnimation(null)
      }, 2000);

      map.setCenter({ lat: Number(location[0]),  lng: Number(location[1])});
      map.setZoom(15);
    }
  }

  // handle seaerch
  const handleSearch = (e) => {
    // const keyword = (e.target.value).replace(/ /g, "");
    const keyword = (e.target.value).trim();
    setSearch(keyword);
  }

  const uploaded = (val)=>{
    setLocations(val);
  }

  const initMap = (url) => {
    fetch.get(url)
    .then(res => {
      setLocations(res.data);
    })
    .catch(er=>{
      console.log(er);
      toastr.error('An error encountered, please try again')
    })
  }

  const getPendingSites = () => {
    initMap(`${process.env.REACT_APP_HOST}/api/site/1/stat`);
  }

  const getApprovedSites = () => {
    initMap(`${process.env.REACT_APP_HOST}/api/site/2/stat`);
  }

  const getRejectedSites = () => {
    initMap(`${process.env.REACT_APP_HOST}/api/site/0/stat`);
  }

  const getDeletedSites = () => {
    initMap(`${process.env.REACT_APP_HOST}/api/site/-1/stat`);
  }
  return (
  <>
  {/* site count */}
  <div className="row">
        <div className="col-md-3 col-sm-6 col-12">
          <SiteCount icon="fas fa-map-marker-alt" title="Total Sites" value={siteCount.TotalSites} bgClass="bg-info" />
        </div>
        <div className="col-md-3 col-sm-6 col-12">
          <SiteCount icon="far fa-thumbs-up" title="Total Approved" value={siteCount.TotalApproved} bgClass="bg-gradient-success" />
        </div>
        <div className="col-md-3 col-sm-6 col-12">
          <SiteCount icon="fas fa-spell-check" title="Total Pending" value={siteCount.TotalPending} bgClass="bg-warning"/>
        </div>
        <div className="col-md-3 col-sm-6 col-12">
          <SiteCount icon="far fa-thumbs-down" title="Total Rejected" value={siteCount.TotalRejected} bgClass="bg-danger" />
        </div>
  </div>

  {/* bread crumbs */}
  <div aria-label="breadcrumb">
      <ol className="breadcrumb">
        <li className="breadcrumb-item">
          <a href="#/" onClick={getPendingSites}>Pending</a>
        </li>
        <li className="breadcrumb-item">
          <a href="#/" onClick={getApprovedSites}>Approved</a>
        </li>
        <li className="breadcrumb-item active" aria-current="page">
          <a href="#/" onClick={getRejectedSites}>Rejected</a>
        </li>
        <li className="breadcrumb-item active" aria-current="page">
          <a href="#/" onClick={getDeletedSites}>Deleted</a>
        </li>
        <li className="flex-grow-1 text-right">
          <a href="#/">
            <ExportXls title={'XlsFile'} data={locations}/>
          </a>
        </li>
      </ol>
  </div>
  <div style={{
    position: 'relative'
  }}>
    {/* add marker */}
    <button className="btn btn-white"
      style={{
        position:'absolute',
        zIndex: 1,
        top:50,
        right: 0,
        margin: 10
      }}
      data-toggle="modal" data-target="#staticBackdrop"
      ><i className="fas fa-map-marker-alt" style={{fontSize: '1.1em', color: 'red'}}></i>
    </button>

    {/* search */}
    <button className="btn btn-white d-nonex" onClick={()=>setFocus()}
    style={{
      position:'absolute',
      zIndex: 1,
      top:100,
      right: 0,
      margin: 10
    }}
    >
      <i className="fas fa-search" style={{fontSize: '.9em'}}></i>
    </button>
    
    {/* upload to cloud */}
    <div
      style={{
        position: 'absolute',
        zIndex: 1,
        top: 150,
        right: 0,
        margin: 10
      }}
    >
        <UploadSites uploaded={uploaded}/>
    </div>
      
      {/* map */}
      <div id="map" style={
        {
          width: '100%',
          height: '80vh',
          position: 'relative'
        }
      }>
      </div>
    {/* search right sidebar */}
    <div
    id="fadeInRight"
    style={{
      position: "fixed",
      width: '240px',
      height: '80%',
      right: 22,
      top: 127,
      zIndex: 1,
      background: 'rgba(255,255,255,1)',
      overflow: "hidden",
    }}
    className={showSearch ? "row active": "row"}
    >
      <div className="container-fluid py-4 mt-3">
        <div className="close" style={{position: 'absolute', top: 10, right: 10, cursor: 'pointer'}}>
          <span aria-hidden="true" onClick={()=>setShowSearch(false)}>×</span>
        </div>
        <div className="input-group col-12">
            {/* <label htmlFor="exampleInputEmail1">First Name</label> */}
            <div className="input-group-prepend">
              <span className="input-group-text bg-primary">
              <i className="fas fa-search" style={{fontSize: '.9em'}}></i>
              </span>
            </div>
            <input type="text" 
            onChange={handleSearch} 
            name="firstName"
            className="form-control" id="exampleInputEmail1" placeholder="search" />
        </div>
        {/* list group */}
        <div className="col-12">
          <div className="list-group-flush mt-2">
            {searchList.map((map, key)=>
              (<button type="button" className="list-group-item list-group-item-action px-0" key={key} onClick={()=>setFocus(map.location)}>
                {map.address}
              </button>)
            )}
          </div>
        </div>
        {/* end group*/}
      </div>
    </div>

    {/* modal */}
    <Modal title="Add New Site" size="modal-md">
      <div className="card-body row">
        <div className="form-group col-sm-12 col-lg-12 col-md-12">
            <label htmlFor="exampleInputEmail1"><a href={process.env.REACT_APP_CDRIVE} target="_blank" rel="noreferrer">Site Docs</a></label>
            <input 
            onChange={handleChange}
            value={site._saqfileurl}
            name="_saqfileurl"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="doc url link" />
        </div>
        <div className="form-group col-sm-12 col-lg-12 col-md-12">
            <label htmlFor="exampleInputEmail1">Site Address</label>
            <input 
            onChange={handleChange}
            value={site.address}
            name="address"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Contact Person</label>
            <input 
            onChange={handleChange}
            value={site.siteContactPerson}
            name="siteContactPerson"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Contact Number</label>
            <input 
            onChange={handleChange}
            value={site.siteContactNum}
            name="siteContactNum"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Coordinates</label>
            <input 
            onChange={handleChange}
            value={site.location}
            name="location"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        <div className="form-group col-sm-12 col-lg-6 col-md-6">
            <label htmlFor="exampleInputEmail1">Coordinator</label>
            <input 
            onChange={handleChange}
            value={site._idcontractor}
            name="_idcontractor"
            type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
        </div>
        {/* Checklist */}
        <div className="col-12 pb-1 mb-4 mt-2 font-weight-bold legend-group text-secondary">
          <span data-toggle="collapse" data-target="#newsitechecklist" className="cursor-pointer">
            <i className="fas fa-tasks"></i> &nbsp;Checklist
          </span>
          
        </div>
        <div className="clearfix"></div>
        <div id="newsitechecklist" className="collapse row">
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="taxDec" value="1" onChange={handleChange}
                  id="taxDec1" />
                  <label htmlFor="taxDec1">
                      Tax Dec/Land Title
                  </label>
              </div>
          </div>
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="taxClearance" value="1" onChange={handleChange}
                  id="taxClearance1"/>
                  <label htmlFor="taxClearance1">
                    Tax Clearance
                  </label>
              </div>
          </div>
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="sketchPlan" value="1" onChange={handleChange}
                  id="sketchPlan1"/>
                  <label htmlFor="sketchPlan1">
                    Sketch Plan
                  </label>
              </div>
          </div>
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="contactPerID" value="1" onChange={handleChange}
                  id="contactPerID1"/>
                  <label htmlFor="contactPerID1">
                      Contact person ID with 3 signature
                  </label>
              </div>
          </div>
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="coordPhoto" value="1" onChange={handleChange}
                  id="coordPhoto1"/>
                  <label htmlFor="coordPhoto1">
                    Coordinates Photo
                  </label>
              </div>
          </div>
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="photoLandOwner" value="1" onChange={handleChange}
                  id="photoLandOwner"/>
                  <label htmlFor="photoLandOwner">
                    Photo with land owner
                  </label>
              </div>
          </div>
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="visinityPlan" value="1" onChange={handleChange}
                  id="visinityPlan"/>
                  <label htmlFor="visinityPlan">
                    Visinity Plan
                  </label>
              </div>
          </div>
          <div className="col-6 form-group">
              <div className="icheck-primary d-inline text-secondary">
                  <input type="checkbox" name="LIS" value="1" onChange={handleChange}
                  id="LIS"/>
                  <label htmlFor="LIS">
                    Lease Information Sheet (LIS)
                  </label>
              </div>
          </div>
          {/* end checklist */}
        </div>
      </div>
      <div className="modal-footer card-footer text-right">
          <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
          
          <button type="button" className="btn btn-primary" onClick={newMarker} >Add</button>
      </div>
    </Modal>
  </div>
  </>);
  
}
 
export default SAQSites;