let timeOutSession = '';

class Auth{
    constructor() {
        this.isAuth = false;
        this.user = '';
        this.role = '';
        this._id = '';
        this.token = '';
        this.userInfo = '';

        // cookie
        this.cookieName = 'jarb';


        // if(localStorage.getItem('user')) {
        // console.log(this.getCookieValue(this.cookieName));

        if(this.getCookieValue(this.cookieName) && localStorage.getItem('user')) {
            const user = JSON.parse(localStorage.getItem('user'));
            this.isAuth = true;
            this._id = user.userId;
            this.user = user.user;
            this.role = user.role;
            this.token = user.token;
            this.userInfo = user;
        }
    }

    login(obj , cb) {
        // console.log(obj);
        localStorage.setItem('user', JSON.stringify(obj));
        this.token = obj.token || '';
        this._id = obj.userId || '';
        this.user = obj.user || '';
        this.role = obj.role || '';
        this.isAuth = true;
        this.userInfo = obj;

        this.setCookieValue(this.cookieName, obj.token);
        this.startTimer();
        return cb();
        
    }

    logout(cb) {
        localStorage.clear();
        this.isAuth = false;
        this.deleteAllCookies();
        return cb();
    }

    


    // user interaction
    startTimer(){
        
        this.clearIdleTime();
        document.body.addEventListener('mousemove', this.startIdleTime);
        document.body.addEventListener('scroll', this.startIdleTime);
        document.body.addEventListener('keydown', this.startIdleTime);
        document.body.addEventListener('click', this.startIdleTime);
        document.body.addEventListener('touchstart', this.startIdleTime);
        window.onblur = () => {
            this.startIdleTime();
        };
         
    }

    startIdleTime(){
        timeOutSession = setTimeout(()=>{ 
            localStorage.clear();
            this.isAuth = false;
        }, 1000 * 60 * 64);
    }

    clearIdleTime(){
        clearTimeout(timeOutSession);
    }
    
    
    // cookies 
    setCookieValue(name, value){
        // cookies
        var now = new Date();
        var time = now.getTime();
        var expireTime = time + 1000 * 60 * 60;
        now.setTime(expireTime);
        // console.log(now.toUTCString());
        document.cookie = `${name}=${value};expires=${now.toUTCString()};path=/;secure`;
    }

    getCookieValue(name){
        return document.cookie.match('(^|;)\\s*' + name + '\\s*=\\s*([^;]+)')?.pop() || ''
    }

    deleteAllCookies() {
        var cookies = document.cookie.split(";");
    
        for (var i = 0; i < cookies.length; i++) {
            var cookie = cookies[i];
            var eqPos = cookie.indexOf("=");
            var name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
            document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT;path=/;secure";
        }
    }
}
export default new Auth();