const SiteCount = ({icon = 'far fa-envelope', title = '', value = 0, bgClass='bg-info'}) => {
  return ( <>
      <div className="info-box">
        <span className={"info-box-icon "+bgClass}><i className={icon}></i></span>

        <div className="info-box-content">
          <span className="info-box-text">{title || ''}</span>
          <span className="info-box-number">{(new Intl.NumberFormat('en-IN', { maximumSignificantDigits: 3 }).format(Number(value)))}</span>
        </div>
        {/* <!-- /.info-box-content --> */}
      </div>  
  </> );
}
 
export default SiteCount;