/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
import { useRef, useState} from "react";
import { useEffect } from "react";
import Auth from "../../Auth";
import DataTable from "../../Components/DataTable";
import Modal from "../../Components/Modal";
// import Thumbnail from "../../Components/Thumbnail";
import fetch from "../../Services/fetch";
import { CONSTANT } from "../../Services/String";
const $ = window.$;
const moment = window.moment;

const SiteTechEvaluation = () => {
    const tblRef = useRef();

    const [status, setStatus] = useState(1);
    const [selected, setSelected] = useState({
      _saqfileurl: '',
      address: '',
      siteContactPerson: '',
      siteContactNum: '',
      location: '',
      _idcontractor: '',
      siteSurvey: 0,
      soilTest: 0,
      siteEvalForm: 0,
      // techEval: 0,
    });
    const [dateRange, setDateRange] = useState({from: moment(new Date()).format('YYYY-MM-DD 00:00:00') , end: moment(new Date()).format('YYYY-MM-DD 23:59:59')});
    const header = [
        {title: 'Address', data: 'address'},
        {title: 'Contact Person', data: 'siteContactPerson'},
        {title: 'Contact Num', data: 'siteContactNum'},
        {title: 'Coordinates', data: 'location'},
        {title: 'Coordinator', data: '_idcontractor'},
        {title: 'Site Files', data: '_saqfileurl',  visible: true,
        render: function ( data, type, row, meta ) {
            return `<a href=${data && data !== 'undefined' ? data : process.env.REACT_APP_CDRIVE} target="_blank">Check File</a>`;
          }
        },
        {title: 'Created By', data: '_createdby'},
        {title: 'Date Created', data: 'created_at', 
          render: function (data) {
            return moment(data).format('YYYY-MM-DD');
          }
        },
        {title: 'Tax Dec', data: 'taxDec',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Tax Clearance', data: 'taxClearance',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Sketch Plan', data: 'sketchPlan',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Land Title', data: 'landTitle',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Coordinates Photo', data: 'coordPhoto',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Photo with land owner', data: 'photoLandOwner',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Visinity Plan', data: 'visinityPlan',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Lease Information Sheet (LIS)', data: 'LIS',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Tech Eval Percentage', data: 'techEvalPercent',  visible: true,
          render: function ( data, type, row, meta ) {
            return (data ? data : '0') +`%`;
          }
        },
        {title: 'Evaluated By', data: '_evaluatedby',  visible: true,},
        {title: 'Date Evaluated', data: 'updated_at',  visible: true,
          render: function(data) {
            return moment(data).format('YYYY-MM-DD');
          }
        },
        {title: 'Status', orderable: false, data: 'status', visible: false,
            render: function(data){
              if(data === 1)
              return 'For approval'
              return data === 2 ? 'Approved': 'Rejected';
            }
        },  
        {title: 'Action By', data: '_actionby',  visible: true,},
        {title: 'Action Date', data: 'action_date',  visible: true,
          render: function(data) {
            return moment(data).format('YYYY-MM-DD');
          }
        }
      ];
    
    
    useEffect(()=>{
        
        getSiteSummary();
        $('#staticBackdrop').on('hidden.bs.modal',(e) => {
        //    setIsUpdate(false); 
        });
        
        //Date range picker
        $('#reservation').daterangepicker({
            opens: 'right'
        }, (start, end, label) => {
            setDateRange({from: start.format('YYYY-MM-DD 00:00:00'), end: end.format('YYYY-MM-DD 23:59:59')});
            getSiteSummary({from: start.format('YYYY-MM-DD 00:00:00'), end: end.format('YYYY-MM-DD 23:59:59')});

        });

    }, [status]);


    // const[isUpdate, setIsUpdate] = useState(false);
    
    
    const getSiteSummary = (date, selectedStat) =>{
      
        let showMsg = true;
        if(!date ) {
            date = dateRange;
            showMsg = false;
        }
        fetch.post(`${process.env.REACT_APP_HOST}/api/site/tech-eval/${date.from}/${date.end}`,{
          status: selectedStat || status
        })
        .then(async res=>{
            // console.log(res.data);
            await tblRef.current.init(res.data);

            $('.site-eval-proceed-btn').on('click', function() {
              const mapId = $(this).data('id');
               changeStatus(mapId, 2);
            });
            // showMsg && window.toastr.success(CONSTANT.SUCCESS);
        }).catch(er=>{
            showMsg && window.toastr.error(CONSTANT.ERRROR);
        });
    }

    const changeStatus = (id, stat) => {
      
      fetch.post(`${process.env.REACT_APP_HOST}/api/site/${id}/update`, {
        status: stat
      })
      .then(res => {
        console.log(res)
      })
      .catch(er=>{})
    }

    const handleChange = (e) =>{
        const target = e.target;
        
        if(target.name === "status"){
          setStatus(()=>{
            return e.target.value
          });
        }else {
            setSelected({...selected, [target.name]: target.checked})
          
        }
        
    }

    const getSelected = (rowData) =>{
      setSelected(rowData);
      $('#staticBackdrop').modal('show');
    }

    const update = () => {
      let percentage = (selected.siteSurvey || 0) + (selected.soilTest || 0) + (selected.siteEvalForm || 0);
      percentage = ((percentage / 3) * 100).toFixed(0);

      const data = {
        siteSurvey: selected.siteSurvey,
        soilTest: selected.soilTest,
        siteEvalForm: selected.siteEvalForm,
        // techEval: selected.techEval,
        techEvalPercent: percentage,
        _evaluatedby: Auth._id, 
        updated_at: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'), 
        status: 1,
      };
      fetch.post(`${process.env.REACT_APP_HOST}/api/site/tech-eval/${selected.location}/update`, data)
      .then(res => {
        tblRef.current.update('location', {...selected, ...data })
        $('#staticBackdrop').modal('hide');
      }) 
    }
    return (<>

        {/* Date range */}
        <div className="row">
            <div className="col-md-4 col-sm-6 col-12">
                <div className="info-box bg-primary px-3">
                    {/* <!-- /.form group --> */}
                    {/* <!-- Date range --> */}
                    <div className="form-group w-100">
                        <label>Date range:</label>

                        <div className="input-group">
                        <div className="input-group-prepend">
                            <span className="input-group-text">
                            <i className="far fa-calendar-alt"></i>
                            </span>
                        </div>
                        <input type="text" className="form-control float-right" id="reservation" />
                        </div>
                        {/* <!-- /.input group --> */}
                    </div>
                    {/* <!-- /.form group --> */}
                </div>
                {/* <!-- /.info-box --> */}
            </div>

            <div className="col-md-4 col-sm-6 col-12">
              <div className="info-box bg-primary px-3 flex-column">
                
                    <div className="form-group">
                      <label>Select Category:</label>
                      <select className="form-control" value={status} onChange={handleChange} name="status">
                        <option value="1">For Approval</option>
                        <option value="2">Approved</option>
                        <option value="0">Rejected</option>
                      </select>
                    </div>
              </div>
            </div>
        </div>

        {/* card */}
        <div className="card mt-3 card-outline card-primary">
            <div className="card-header">
            <h3 className="card-title">Site Technical Evaluation Summary Table</h3>
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body">
                <DataTable 
                getSelected={getSelected}
                header={header}
                ref={tblRef}
                buttons={true}
                />
            </div>
            {/* <!-- /.card-body --> */}
        </div>
        {/* <!-- /.card --> */}


        {/* <!-- Modal --> */}
        <Modal title="Site Technical Information" size="modal-md">
            <div className="card-body row">
                <div className="form-group col-sm-12 col-lg-12 col-md-12">
                    <label htmlFor="exampleInputEmail1">
                       Site Docs/<a href={selected._saqfileurl ? selected._saqfileurl : process.env.REACT_APP_CDRIVE} target="_new">Check Docs</a>
                     </label>
                     <span className="float-right">{`${selected.techEvalPercent || 0}% completed`}</span>
                    <input 
                    value={selected._saqfileurl || ''}
                    readOnly
                    name="_saqfileurl"
                    type="text" className="form-control" id="exampleInputEmail1" placeholder="doc url link" />
                </div>
                <div className="form-group col-sm-12 col-lg-12 col-md-12">
                    <label htmlFor="exampleInputEmail1">Site Address</label>
                    <input 
                    value={selected.address || ''}
                    readOnly
                    name="address"
                    type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                </div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputEmail1">Contact Person</label>
                    <input 
                    value={selected.siteContactPerson || ''}
                    readOnly
                    name="siteContactPerson"
                    type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                </div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputEmail1">Contact Number</label>
                    <input 
                    value={selected.siteContactNum || ''}
                    readOnly
                    name="siteContactNum"
                    type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                </div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputEmail1">Coordinates</label>
                    <input 
                    value={selected.location || ''}
                    readOnly
                    name="location"
                    type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                </div>
                <div className="form-group col-sm-12 col-lg-6 col-md-6">
                    <label htmlFor="exampleInputEmail1">Coordinator</label>
                    <input 
                    value={selected._idcontractor || ''}
                    readOnly
                    name="_idcontractor"
                    type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                </div>
                <div className={selected.status !== 0? "d-none" : "col-lg-12"}>
                    <div className="form-group">
                      <label>Remarks</label>
                      <textarea className="form-control" rows="3" placeholder="Type here ..." 
                      name="actionRemarks" value={selected.actionRemarks} readOnly>
                      </textarea>
                    </div>
                </div>
                {/* Checklist */}
                <div className="col-12 pb-1 mb-4 mt-2 font-weight-bold legend-group text-secondary">
                  <span data-toggle="collapse" data-target="#newsitechecklist" className="cursor-pointer">
                    <i className="fas fa-tasks"></i> &nbsp;Checklist
                  </span>
                </div>
                <div className="clearfix"></div>
                <div id="newsitechecklist" className="collapse row">
                  <div className="col-6 form-group">
                      <div className="icheck-primary d-inline text-secondary">
                          <input type="checkbox" name="siteSurvey" value="1" onChange={handleChange}
                          checked={selected.siteSurvey || false}
                          id="siteSurvey" />
                          <label htmlFor="siteSurvey">
                              Site Survey
                          </label>
                      </div>
                  </div>
                  <div className="col-6 form-group">
                      <div className="icheck-primary d-inline text-secondary">
                          <input type="checkbox" name="soilTest" value="1" onChange={handleChange}
                          checked={selected.soilTest || false}
                          id="soilTest"/>
                          <label htmlFor="soilTest">
                            Soil Test Result
                          </label>
                      </div>
                  </div>
                  <div className="col-6 form-group">
                      <div className="icheck-primary d-inline text-secondary">
                          <input type="checkbox" name="siteEvalForm" value="1" onChange={handleChange}
                          checked={selected.siteEvalForm || false}
                          id="siteEvalForm"/>
                          <label htmlFor="siteEvalForm">
                            Site Technical Evaluation Form
                          </label>
                      </div>
                  </div>
                  {/* <div className="col-6 form-group">
                      <div className="icheck-primary d-inline text-secondary">
                          <input type="checkbox" name="techEval" value="1" onChange={handleChange}
                          checked={selected.techEval || false}
                          id="techEval"/>
                          <label htmlFor="techEval">
                            Technical Evaluation
                          </label>
                      </div>
                  </div> */}
                </div>
                {/* end checklist */}
            </div>
            {/* <!-- /.card-body --> */}
            <div className="modal-footer card-footer text-right">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" className={selected.status !== 1 && selected.status !== null? "d-none" : "btn btn-primary"} onClick={update}>Update</button>
            </div>
        </Modal>
    </>);
}
 
export default SiteTechEvaluation;